.editor-wrapper.square {
  position: relative;
  #map {
    border: 20px solid white;
  };

  * {
    font-family: Rubik;
  }
  .headline-text {
    position: relative;
    font-size: 70px;
    color: #212121;
    line-height: normal;
    z-index: 2;
    margin-bottom: 25px;
  }

  .secondary-text {
    position: relative;
    font-size: 30px;
    font-weight: 300;
    z-index: 2;
    color: #606060;
    margin-top: 130px;
  }

  .text-wrapper {
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    text-align: center;
    padding-bottom: 90px;
  }
}
